@tailwind base;
@tailwind components;
@tailwind utilities;
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.header {
  position: fixed;
  top: 0%;
  left: 0%;
  width: 100%;
  padding: 40px 100px;
  z-index: 100000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: 0.5s;
}

.header .logo{
  color: #fff;
  font-weight: 700;
  font-size: 2em;
  text-decoration: none;
}

.header .logo span{
  color: #ff0157;
}
.header .navigation{
  position: relative;
  display: flex;
}
.header .navigation li{
  list-style: none;
  margin-left: 30px;
}
.header .navigation li a{
  text-decoration: none;
  color: #fff;
  font-weight: 300;
}

.sticky{
  background: #fff;
  padding: 10px 100px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.05);
}
.sticky .logo{
   color: #111;
}
.sticky .navigation li a{
color: #111;
}

.sidebar{
  width: 250px;
  height: 100vh;
  position: fixed;
  right: -250px;
  top: 0;
  z-index: 2;
  background:#f67c92;
}
.sidebar ul li{
  list-style: none;
  margin:40px 20px;
  color: white;
  font-size: 20px;
}
.ssm{
  right: 0px;
}

@media screen and (max-width:600px) {
  .media{
    flex-direction: column;
  }
  .wm{
    width: 100%;
  }
  .mm{
    margin: 15px;
  }
  .mn{
    margin: 0%;
  }
  .wms{
    width: 103%;
  }
}
